
<script>
    export default{
        data () {
            return {
                names:["wangchao","guotong","jiaozhu"],
                nowhu:1
            }
        },
        methods: {
            morebuttons(){
                this.names.push("xixixi"+this.nowhu)
                this.nowhu++
            }
        }
    }
</script>

<template>
    <h3 class="container">基础组件构成</h3>
    <Button type="button" @click="morebuttons">添加数据</Button>
    <ul>
        <li v-for="(name,index) in names" :key="index" @click="morebuttons">{{name}}</li>
    </ul>
</template>

<style>
.container{
    font-size: 80px;
    color: blanchedalmond;
}
</style>